import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import StoryView from '../views/StoryView.vue';
import ResuiltView from '../views/Result.vue'
import VueNumber from 'vue-number-animation'

Vue.use(VueNumber)
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/story',
    name: 'story',
    component: StoryView
  },
  {
    path:'/result',
    name:'result',
    component:ResuiltView
  }
]

const router = new VueRouter({
  //mode: 'history',
  base: 'grain_test',
  routes
})


export default router
