                url: "",
            });
            console.log("공유 성공");
        } catch (e) {
            var ans=confirm('url 복사하시겠습니까?');
            if(ans){
                var url = '';
                var textarea = document.createElement("textarea");
                document.body.appendChild(textarea);
                url = window.document.location.origin+window.document.location.pathname;
                textarea.value = url;
                textarea.select();
                document.execCommand("copy");
                document.body.removeChild(textarea);
            }
        }
